.Form {
    border: none;
    border-bottom: 2px #00d1ff solid;
  }
  
  .Button {
    border: none;
    background-color: var(--bg-color);
    color: var(--main-text);
    border-radius: 100px;
    padding: 4px 10px;
    min-width: 75px;
    margin: 5px;
  }
  
  .Button:hover {
    cursor: pointer;
    background-color: var(--card-color);
    color: #fff;
  }